import { call, put, takeEvery } from "redux-saga/effects"
import {getRatesHistoryFail, getRatesHistorySuccess, toggleLoading} from "./actions";
import {GET_RATES_HISTORY} from "./actionTypes";
import {getRatesHistory} from "../../helpers/backend_helper";

// Crypto Redux States

//Include Both Helper File with needed methods

function* fetchHistory({ payload: currency }) {
  try {
    const ratesHistory = [];
    yield put(toggleLoading())
    const response = yield call(getRatesHistory, currency)
    const { prices } = response; // price[0] - date string; price[1] - price vs USD
    // eslint-disable-next-line array-callback-return
    prices.map((price) => {
      const value = parseFloat(price[1].toFixed(2))
      ratesHistory.push({
        time: price[0],
        value,
      })
    })
    ratesHistory.sort((a, b) => new Date(a.time) - new Date(b.time))
    yield put(getRatesHistorySuccess(currency, ratesHistory))
    yield put(toggleLoading())
  } catch (error) {
    yield put(getRatesHistoryFail(error))
    yield put(toggleLoading())
  }
}

function* ratesHistorySaga() {
  yield takeEvery(GET_RATES_HISTORY, fetchHistory)
}

export default ratesHistorySaga
