import React, {useState} from "react";
import {useTranslation} from "react-i18next";
import { useForm } from "react-hook-form";
import {Spinner} from "reactstrap";
import {toast} from "react-toastify";

import {ZENDESK_URL} from "../../helpers/url_helper";
import {ZENDESK_TOKEN} from "../../common/constants";
import {validateEmail} from "../../common/helpers";

const Support = () => {
	const { t } = useTranslation();
	const [loading, setLoading] = useState(false);
	const { register, handleSubmit, reset, formState: { errors } } = useForm();

	const submit = (data) => {
		setLoading(true)

		const objForTicket = {
			request: {
				requester: {
					name: data.name,
					email: data.email,
				},
				subject: data.topic,
				comment: {
					body: data.text,
				},
			},
		};

		fetch(ZENDESK_URL, {
			method: "POST",
			headers: {
				"Content-Type": "application/json",
				Authorization:
					"Basic " +
					btoa(`${data.email}/token:${ZENDESK_TOKEN}`),
			},
			body: JSON.stringify(objForTicket),
		})
			.then((res) => {
				if (res.status !== 201) {
					throw new Error();
				}
				reset({
					name: '',
					email: '',
					topic: '',
					text: '',
				})
				setLoading(false);
			})
			.catch(() => {
				toast.error(t('Произошла ошибка. Попробуйте позже!'))
				setLoading(false)
			});
	}

	return (
		<section className="support">
			<div className="support__form">
				<h1 className="support__title">
					{t('Поддержка')}
				</h1>
				<form
					onSubmit={handleSubmit(submit)}
					noValidate
				>
					<div className="form-group" >
						<input
							name="name"
							placeholder={t('Ваше имя')}
							{...register('name', {
								required: true
							})}
						/>
						{errors.name && <p className="invalid-feedback">{t('Поле необходимо заполнить!')}</p>}
					</div>
					<div className="form-group" >
						<input
							name="email"
							placeholder="E-Mail"
							type="email"
							{...register('email', {
								validate: async (value) => {
									return validateEmail(value)
								}
							})}/>
						{errors.email && <p className="invalid-feedback">{t('Невалидный E-Mail!')}</p>}
					</div>
					<div className="form-group" >
						<input name="topic" placeholder={t('Тема вашего запроса')} {...register('topic', { required: true })}/>
						{errors.topic && <p className="invalid-feedback">{t('Поле необходимо заполнить!')}</p>}
					</div>
					<div className="form-group" >
						<textarea name="text" placeholder={t('Текст сообщения')} {...register('text', { required: true })}/>
						{errors.text && <p className="invalid-feedback">{t('Поле необходимо заполнить!')}</p>}
					</div>
					<button className="support__submit btn" disabled={loading}>
						{
							loading ?
								<Spinner color="light" className="loading" /> :
								t('Отправить')
						}
					</button>

				</form>
			</div>
		</section>
	)
}

export default Support;
