import React, {useEffect} from "react";
import { createChart } from 'lightweight-charts';
import {connect} from "react-redux";


const formatter = new Intl.NumberFormat('en-US', {
	style: 'currency',
	currency: 'USD',
	currencyDisplay: 'code',
});

const Slide = (props) => {

	const { icon, currKey, currency, diff, price, id } = props;
	const ratesHistory = id === 'torexo' ? props.history.tether : props.history[currency.toLowerCase()];

	useEffect(() => {
		ratesHistory.splice(-2)
		ratesHistory.push({
			time: Date.now(),
			value: id === 'torexo' ? 1 : price,
		})
		const graphContainer = document.querySelector(`.currency-graph.${id}`);

		if (graphContainer.children) {
			graphContainer.innerHTML = '';
		}

		const chart = createChart(graphContainer, {
			leftPriceScale: {
				visible: false,
			},
			rightPriceScale: {
				visible: false,
			},
			timeScale: {
				visible: false,
			},
			grid: {
				vertLines: {
					visible: false,
				},
				horzLines: {
					visible: false,
				},
			},
			layout: {
				backgroundColor: 'rgba(47, 50, 65, 0.5)',
			},
		});

		const areaSeries = chart.addAreaSeries({
			topColor: diff < 0 ? 'rgba(252, 226, 237, 0.38)' : 'rgba(188, 251, 255, 0.4)',
			bottomColor: diff < 0 ? 'rgba(44, 44, 44, 0)' : 'rgba(0, 0, 0, 0)',
			lineColor:  diff < 0 ? '#FA2256' : '#11CABE',
			lineWidth: 1,
		});

		areaSeries.setData(ratesHistory);
		}, [price, ratesHistory, diff, id]);

	const renderDiff = (diff) => {
		if (diff < 0) {
			return (
				<span className="slide__diff down">
					{diff * -1}&#37;
				</span>
			)
		}
		if (diff === 0) {
			return (
				<span className="slide__diff">
					{diff}&#37;
				</span>
			)
		}
		return (
			<span className="slide__diff up">
				{diff}&#37;
			</span>
		)
	}

	return (
		<div className={`slide${id === "torexo" ? ' torexo' : ''}`}>
			<div className="slide__top-block">
				{/*Hardcode for torexo*/}
				{
					id !== 'torexo' ?
						<img className="currency-icon" src={icon} alt={currKey}/> :
						<p className="currency-icon">TD</p>
				}
				<div className={`currency-graph ${id}`} />
			</div>
			{renderDiff(diff)}
			<p className="slide__currency">{currency}</p>
			<div className="slide__bottom-block bottom-block">
				<span className="bottom-block__price">{formatter.format(price)}</span>
				<p className="bottom-block__key">{currKey}</p>
			</div>
		</div>
	)
}

const mapStateToProps = ({ ratesHistory }) => ({
	history: ratesHistory,
})

export default connect(
	mapStateToProps,
	null
)(Slide)
