import React, {useEffect, useState} from "react";
import {useTranslation} from "react-i18next";

import {P2P_DASHBOARD_URL, P2P_EXCHANGE_URL} from "../../helpers/url_helper";
import InputDropdown from "./InputDropdown";

const formatter = new Intl.NumberFormat('en-US');

const FROM_CURRENCIES = {
	USDT: "usdt",
	BTC: "btc",
	ETH: "eth",
	LTC: "ltc",
}
const TO_CURRENCIES = {
	RUB: "rub",
	EUR: "eur",
	USD: "usd",
	USDT: "usdt",
	BTC: "btc",
	ETH: "eth",
	LTC: "ltc",
}

const Widget = () => {
	const { t } = useTranslation();
	const [direction, setDirection] = useState('buy');
	const [rate, setRate] = useState(1);
	const [currencyFrom, setCurrencyFrom] = useState(FROM_CURRENCIES.USDT)
	const [fromValue, setFromValue] = useState(0);
	const [currencyTo, setCurrencyTo] = useState(TO_CURRENCIES.BTC)
	const [toValue, setToValue] = useState(1);
	const [loading, setLoading] = useState(false);

	const getRate = () => {
		setLoading(true)
		fetch(P2P_EXCHANGE_URL.replace(':to', currencyTo).replace(':from', currencyFrom))
			.then((res) => res.json())
			.then((data) => {
				const { value } = data;
				setRate(value)
				setFromValue(parseFloat((value * toValue).toFixed(2)))
				setLoading(false)
			})
	}

	useEffect(() => {
		getRate();
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [currencyTo, currencyFrom]);

	const handleChangeFrom = (value) => {
		setFromValue(value)
		if (currencyTo === 'eur' || currencyTo === 'usd' || currencyTo === 'rub' || currencyTo === 'usdt') {
			setToValue(parseFloat((value / rate).toFixed(2).replace(/0+$/, "")))
		} else {
			setToValue(parseFloat((value / rate).toFixed(8).replace(/0+$/, "")))
		}
	}
	const handleChangeTo = (value) => {
		setToValue(value)
		if (currencyFrom === 'usdt') {
			setFromValue(parseFloat((value * rate).toFixed(2).replace(/0+$/, "")))
		} else {
			setFromValue(parseFloat((value * rate).toFixed(8).replace(/0+$/, "")))
		}
	}

	const handleBtnClick = () => {
		const url = new URL(P2P_DASHBOARD_URL);
		if (direction === 'buy') {
			url.searchParams.append("from_currency", currencyFrom);
			url.searchParams.append("to_currency", currencyTo);
			url.searchParams.append("amount", toValue.toString());
		} else {
			url.searchParams.append("from_currency", currencyTo);
			url.searchParams.append("to_currency", currencyFrom);
			url.searchParams.append("amount", fromValue.toString());
		}
		window.open(url.toString(), '_blank')
	}

	return (
		<div className="widget">
			<div className="widget__toggle toggle">
				<p
					className={`toggle__button ${direction === 'buy' && 'active'}`}
					onClick={() => setDirection('buy')}
				>
					{t('Купить')}
				</p>
				<p
					className={`toggle__button ${direction === 'sell' && 'active'}`}
					onClick={() => setDirection('sell')}
				>
					{t('Продать')}
				</p>
			</div>
			<div className="widget__info">
				<p>1 {currencyTo.toUpperCase()} {t('приблизительно')}</p>
				<p className="amount"><span>{formatter.format(rate)}</span> {currencyFrom.toUpperCase()}</p>
			</div>
			<div className="widget__params">
				<InputDropdown
					inputValue={fromValue}
					disabled={loading}
					dropdownValue={currencyFrom}
					onChangeInput={(value) => handleChangeFrom(value)}
					onChangeDropdown={(currency) => setCurrencyFrom(currency)}
					dropdownList={FROM_CURRENCIES}
				/>
				<InputDropdown
					inputValue={toValue}
					disabled={loading}
					dropdownValue={currencyTo}
					onChangeInput={(value) => handleChangeTo(value)}
					onChangeDropdown={(currency) => setCurrencyTo(currency)}
					dropdownList={TO_CURRENCIES}
				/>
			</div>
			<button
				className="widget__buy"
				onClick={() => handleBtnClick()}
			>
				{direction === 'buy' ? t('Купить сейчас') : t('Продать сейчас')}
			</button>
			<p className="we-accept">
				{t('Мы принимаем')}
			</p>
			<ul className="widget__footer">
				<li className="item mc" />
				<li className="item visa" />
				<li className="item ap" />
				<li className="item gp" />
				<li className="item pp" />
			</ul>
		</div>
	)
}
export default Widget;
