import React, {useEffect, useState} from "react";
import {useTranslation} from "react-i18next";
import {getNews} from "../../helpers/backend_helper";
import {serverPath} from "../../common/serverPath";

const News = () => {
	const { t, i18n } = useTranslation();
	const [news, setNews] = useState([])
	const [activeSource, setActiveSource] = useState({})
	const path = serverPath.methods.getServerPath();

	useEffect(() => {
		getNews()
			.then((res) => {
				setNews(res);
			})
			.catch((err) => {
				setNews(null)
			})
	}, [])

	useEffect(() => {
		if (news.length) {
			setActiveSource(news[0])
		}
	}, [news]);


	const prevActiveSource = () => {
		if (news.indexOf(activeSource) > 0) {
			setActiveSource(news[news.indexOf(activeSource) - 1])
		}
	}

	const nextActiveSource = () => {
		if (news.indexOf(activeSource) < 3) {
			setActiveSource(news[news.indexOf(activeSource) + 1])
		}
	}

	return (
		<section className={`news section ${!news.length && 'hidden'}`}>
			{
				news.length &&
				<React.Fragment>
					<div className="news__info">
						<h2 className="news__title section-title">
							{t('Что пишут о нас в СМИ?')}
						</h2>
						<p className="new-text">
							&#8220;
							{
								i18n.language === 'ru' ?
									activeSource.text_ru :
									activeSource.text_en
							}
							&#8220;
						</p>
						<p className="new-source">
							{activeSource.name}, {new Date(activeSource.date).getFullYear()}
						</p>
						<div className="news__nav">
							<button className="prev"
							        onClick={() => prevActiveSource()}
							/>
							<button className="next"
							        onClick={() => nextActiveSource()}
							/>
						</div>
					</div>
					<ul className="news__switcher">
						<li className={`source first ${news.indexOf(activeSource) === 0 && 'active'}`}
						    onClick={() => window.open(news[0].url, '_blank')}
						>
							<img src={path + news[0].file} alt={news[0].name} />
						</li>
						<li className={`source second ${news.indexOf(activeSource) === 1 && 'active'}`}
						    onClick={() => window.open(news[1].url, '_blank')}
						>
							<img src={path + news[1].file} alt={news[1].name} />
						</li>
						<li className={`source third ${news.indexOf(activeSource) === 2 && 'active'}`}
						    onClick={() => window.open(news[2].url, '_blank')}
						>
							<img src={path + news[2].file} alt={news[2].name} />
						</li>
						<li className={`source fourth ${news.indexOf(activeSource) === 3 && 'active'}`}
						    onClick={() => window.open(news[3].url, '_blank')}
						>
							<img src={path + news[3].file} alt={news[3].name} />
						</li>
					</ul>
				</React.Fragment>
			}
		</section>
	)
}

export default News;
