import React, {useEffect} from 'react'
import { connect } from "react-redux";

import MainInfo from "./MainInfo";
import Carousel from "./Carousel";
import Trust from "./Trust";
import OneClickExchange from "./OneClickExchange";
import Trustful from "./Trustful";
import News from "./News";
import Video from "./Video";
import Bonus from "./Bonus";
import {getRatesHistory} from "../../store/ratesHistory/actions";
import {currenciesForGraphs} from "../../common/constants";

const Home = (props) => {

	const { magazinesVisibility } = props;

	useEffect(() => {
		// eslint-disable-next-line array-callback-return
		Object.keys(currenciesForGraphs).map((currency) => {
			props.onGetHistory(currency);
		})
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, []);

	return (
		<div className="main-wrapper">
			<div className="bg-grid-top">
				<MainInfo />
				<Carousel />
			</div>
			<Trust />
			<OneClickExchange />
			<Trustful />
			{ magazinesVisibility && <News /> }
			<Video />
			<Bonus />
		</div>
	)
}

const mapStateToProps = ({ settings }) => ({
	magazinesVisibility: settings.visibility.magazines
})

const mapDispatchToProps = dispatch => ({
	onGetHistory: (currency) => dispatch(getRatesHistory(currency)),
})

export default connect(
	mapStateToProps,
	mapDispatchToProps
)(Home)
