import React, {useEffect, useState} from "react";
import {getBlogPost} from "../../../helpers/backend_helper";
import {useTranslation} from "react-i18next";
import {useParams} from "react-router";
import {Spinner} from "reactstrap";
import {serverPath} from "../../../common/serverPath";
import moment from "moment";

const BlogPost = () => {
	const { t, i18n } = useTranslation();
	const {uri} = useParams();
	const [loading, setLoading] = useState(false)
	const [post, setPost] = useState({})
	const path = serverPath.methods.getServerPath();

	useEffect(() => {
		setLoading(true)
		getBlogPost(i18n.language === 'ru' ? 'ru' : 'en', uri)
			.then((res) => {
				setPost(res[0])
				setLoading(false)
			})
	}, [i18n.language, uri]);

	const renderPost = () => {
		if (!post) {
			return (
				<div>
					<p className="empty">
						{t('Пост не найден. Попробуйте позже, или прочитайте другой пост')}...
					</p>
				</div>
			)
		}
		const { alt, date, description, file, name } = post;
		return (
			<React.Fragment>
				<h1 className="blog-post__title">
					{name}
				</h1>
				<div className="blog-post__img-container">
					<img src={path + file} alt={alt} />
				</div>
				<div className="blog-post__info" dangerouslySetInnerHTML={{ __html: description }} />
				<span className="blog-post__date">
					{moment(date).locale(i18n.language).format('DD.MM.YYYY')}
				</span>
			</React.Fragment>
		)
	}

	return (
		<section className="blog-post">
			{
				loading ?
					<Spinner className="loading" color="primary"/> :
					renderPost()
			}
		</section>
	)
}

export default BlogPost;
