import {GET_RATES_HISTORY_FAIL, GET_RATES_HISTORY_SUCCESS} from "./actionTypes"
import {TOGGLE_LOADING} from "./actionTypes";
import {currenciesForGraphs} from "../../common/constants";

const INIT_STATE = {
  loading: false,
  error: null,
}
// eslint-disable-next-line array-callback-return
Object.keys(currenciesForGraphs).map((currency) => {
  INIT_STATE[currency] = [];
})

const settings = (state = INIT_STATE, action) => {
  const { payload } = action;
  switch (action.type) {
    case TOGGLE_LOADING:
      return {
        ...state,
        loading: !state.loading,
      }

    case GET_RATES_HISTORY_SUCCESS:
      return {
        ...state,
        [payload.currency]: payload.history,
      }

    case GET_RATES_HISTORY_FAIL:
      return {
        ...state,
        error: action.payload.error,
      }

    default:
      return state;
  }
}

export default settings;
