import React, {useEffect, useState} from "react";
import {useTranslation} from "react-i18next";
import {connect} from "react-redux";
import {Modal, ModalBody, ModalHeader, Spinner} from "reactstrap";

const Video = (props) => {
	const { videos, companyName } = props;
	const { t, i18n } = useTranslation();
	const [modal, setModal] = useState(false);
	const [videoUrl, setVideoUrl] = useState('');

	useEffect(() => {
		if (videos.length) {
			let URL = '';
			if (i18n.language === 'ru') {
				URL = videos.find((video) => video.language.languageLocale === 'ru').link;
			} else {
				URL = videos.find((video) => video.language.languageLocale === 'en').link;
			}
			setVideoUrl(URL)
		}
	}, [videos, i18n.language, props]);

	const toggleModal = () => setModal(!modal);

	return (
		<section className="video section">
			<div className="video__video-block">
				<div className="video__blank" onClick={() => toggleModal()}/>
			</div>
			<div className="video__info">
				<h2 className="video__title section-title">
					{t('Посмотрите промо видео об {{ companyName }}', { companyName })}
				</h2>
				<p className="video__description section-description">
					{t('В нем вы узнаете все полезную информацию, которая понадобиться вам, для использования нашей платформы')}
				</p>
			</div>
			<div className="line-bottom-right" />
			<Modal centered size="lg" isOpen={modal} toggle={toggleModal}  modalClassName="video-modal">
				<ModalHeader toggle={toggleModal} />
				<ModalBody>
					{
						videos.length ?
							<iframe
								width="100%"
								height="400px"
								src={videoUrl}
						        title="YouTube video player" frameBorder="0"
						        allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
						        allowFullScreen
							/>:
							<Spinner color="primary" className="loading" />
					}
				</ModalBody>
			</Modal>
		</section>
	)
}

const mapStateToProps = ({ settings }) => ({
	videos: settings.video,
	companyName: settings.branding.companyName,
})

export default connect(
	mapStateToProps,
	null
)(Video)
