import React from "react";

import comfort from '../../assets/images/comfort-icon.svg'
import safety from '../../assets/images/safety-icon.svg'
import p2p from '../../assets/images/p2p-icon.svg'
import {useTranslation} from "react-i18next";
import {connect} from "react-redux";

const Trust = (props) => {
	const { t, i18n } = useTranslation();
	const { start } = props.buttonsUrl[i18n.language];

	const handleStart = () => {
		window.open(start, '_blank')
	}

	return (
		<section className="trust section">
			<div className="trust__cards">
				<div className="card">
					<div className="img-wrapper">
						<img src={comfort} alt="Удобно!" />
					</div>
					<h4 className="card__title">
						{t('Удобство')}
					</h4>
					<p className="card__description">
						{t('Покупайте и продавайте цифровые валюты, отслеживайте их в одном месте')}
					</p>
				</div>
				<div className="card">
					<div className="img-wrapper">
						<img src={p2p} alt="P2P!" />
					</div>
					<h4 className="card__title">
						{t('P2P площадка')}
					</h4>
					<p className="card__description">
						{t('Покупка или продажа криптовалюты за фиатные деньги')}
					</p>
				</div>
				<div className="card">
					<div className="img-wrapper">
						<img src={safety} alt="Безопасно!" />
					</div>
					<h4 className="card__title">
						{t('Безопасность')}
					</h4>
					<p className="card__description">
						{t('Все сделки защищены ESCROW и Social Trust Scoring')}
					</p>
				</div>
			</div>
			<div className="trust__info info">
				<h2 className="info__title section-title">
					{t('Платформа, которой доверяют')}

				</h2>
				<p className="info__description section-description">
					{t('С нами больше никаких длительных ожиданий, огромных издержек и рисков мошеннических действий')}
				</p>
				<button
					className="info__btn"
					onClick={() => handleStart()}
				>
					{t('Хочу попробовать')}
				</button>
			</div>
		</section>
	)
}

const mapStateToProps = ({ settings }) => ({
	buttonsUrl: settings.buttonsUrl,
})

export default connect(
	mapStateToProps,
)(Trust)
