import React from "react";
import { serverPath } from '../../common/serverPath';
import {Link} from "react-router-dom";
import {BLOG_POST_PATH} from "../../common/routerConstants";

const BlogItem = (props) => {
	const { title, image, uri } = props;
	const path = serverPath.methods.getServerPath();

	return (
		<Link
			className="blog-item"
			style={{ backgroundImage: `url("${path}${image}")` }}
			to={BLOG_POST_PATH.replace(':uri', uri)}
		>
			<h3 className="title">
				{title}
			</h3>
		</Link>
	)
}

export default BlogItem
