import React, {useState} from "react";
import {Dropdown, DropdownItem, DropdownMenu, DropdownToggle} from "reactstrap";

const InputDropdown = (props) => {
	const [dropdownOpen, setDropdownOpen] = useState(false);
	const { inputValue, dropdownValue, onChangeInput, onChangeDropdown, dropdownList, disabled } = props;

	const toggleOpen = () => setDropdownOpen(prevState => !prevState);

	return (
		<div className="param-container">
			<input
				type="number"
				value={inputValue}
				disabled={disabled}
				onChange={(e) => onChangeInput(e.target.value)}
			/>
			<div className="dropdown-container">
				<Dropdown isOpen={dropdownOpen} toggle={toggleOpen}>
					<DropdownToggle caret className={`${dropdownValue}-icon-before`}>
						{dropdownValue.toUpperCase()}
					</DropdownToggle>
					<DropdownMenu container="body">
						{
							Object.keys(dropdownList).map((key) => {
								return (
									<DropdownItem
										key={`dropdown-item-${key}`}
										className={`${dropdownList[key]}-icon-before`}
										onClick={() => onChangeDropdown(dropdownList[key])}
									>
										{dropdownList[key].toUpperCase()}
									</DropdownItem>
								)

							})
						}
					</DropdownMenu>
				</Dropdown>
			</div>
		</div>

	)
}

export default InputDropdown;
