import React, {useEffect, useState} from "react";
import {useTranslation} from "react-i18next";
import {getBlogPosts} from "../../helpers/backend_helper";
import {Spinner} from "reactstrap";
import BlogItem from "./BlogItem";

const Blog = () => {
	const { t, i18n } = useTranslation();
	const [loading, setLoading] = useState(false);
	const [blogList, setBlogList] = useState([]);

	useEffect(() => {
		setLoading(true)
		getBlogPosts(i18n.language === 'ru' ? i18n.language : 'en')
			.then((res) => {
				setBlogList(res)
				setLoading(false)
			})
	}, [i18n.language]);

	const renderBlogList = () => {
		if (!blogList.length) {
			return (
				<div>
					<p className="empty">
						{t('Нет записей')}...
					</p>
				</div>

			)
		}
		return (
			<div className="bloglist">
				{
					blogList.map((post) => {
							return <BlogItem
								key={post._id}
								title={post.name + blogList.indexOf(post)}
								image={post.file}
								uri={post.postUrl}
							/>
						})
				}
			</div>
		)
	};

	return (
		<section className="blog">
			<h1 className="blog__title">
				{t('Криптовалюты. Трейдинг.')}
			</h1>
			<p className="blog__subtitle">
				{t('Мы добавляем сюда только самую важную и актуальную информацию, чтоб вы были в курсе всех событий и могли преуспеть на нашей платформе')}
			</p>
			{loading ? <Spinner className="loading" color="primary"/> : renderBlogList()}
		</section>
	)
}

export default Blog;
