import React, {useEffect, useState} from "react";
import {useTranslation} from "react-i18next";
import {getDocs} from "../../helpers/backend_helper";
import {serverPath} from "../../common/serverPath";
import {connect} from "react-redux";
import {Link} from "react-router-dom";
import {HOME_PATH} from "../../common/routerConstants";

const Footer = (props) => {
	const { t, i18n } = useTranslation();
	const { socialLinks, companyInfo, subscribeVisibility, bottomDocs, logo, companyName } = props;
	const [privacyPolicyLink, setPrivacyPolicyLink] = useState('')
	const [usersAgreementLink, setUsersAgreementLink] = useState('')
	const { subscribe } = props.buttonsUrl[i18n.language];
	const [docsList, setDocsList] = useState([]);
	const path = serverPath.methods.getServerPath();

	useEffect(() => {
		getDocs(i18n.language === 'ru' ? 'ru' : 'en')
			.then((res) => {
				setDocsList(res);
			})
	}, [i18n.language]);

	useEffect(() => {
		if (Object.keys(bottomDocs).length) {
			setPrivacyPolicyLink(path + bottomDocs.privacyPolicy[i18n.language === 'ru' ? 'ru' : 'en'].file)
			setUsersAgreementLink(path + bottomDocs.usersAgreement[i18n.language === 'ru' ? 'ru' : 'en'].file)
		}
	}, [bottomDocs, i18n.language, path]);


	const handleSubscribeClick = () => {
		window.open(subscribe, '_blank')
	}

	return (
		<div className="footer">
			<div className="footer__top-block">
				<div className="footer__brand-block top-block__item">
					{
						logo &&
						<Link to={HOME_PATH} className="logo">
							<img src={path + logo} alt="logo" />
						</Link>
					}
					<p className="moto">
						{t('Ваш лучший криптопартнер!')}
					</p>
					{
						companyInfo ?
							<p className="address">
								{companyInfo.name}
								<br className="all"/>{companyInfo.address_line_one}
								<br className="all"/>{companyInfo.address_line_two}
							</p> :
							null
					}
					<ul className="social-links">
						{
							socialLinks ?
								socialLinks.map((socialMedia) => {
									return (
										<li
											key={socialMedia._id}
											className={`social-link ${socialMedia.type}`}
											onClick={() => window.open(socialMedia.link, '_blank')}
										/>
									)
								}) :
								null
						}
					</ul>
				</div>
				<div className="footer__links-block top-block__item">
					<h3 className="links-title footer-title">
						{t('Полезные ссылки')}
					</h3>
					<ul className="links">
						{
							docsList ?
								docsList.map((doc) => {
									return (
										<li
											key={doc._id}
											className="link"
										>
											<a href={path + doc.file} target="_blank" rel="noreferrer">{doc.name}</a>
										</li>
									)
								}) :
								null
						}
					</ul>
				</div>
				{
					subscribeVisibility ?
						<div className="footer__subscribe-block top-block__item">
							<h3 className="subscribe-title footer-title">
								{t('Подпишитесь на обновления')}
							</h3>
							<p className="subscribe-description">
								{t('Подпишитесь, чтобы получать уведомления о последних изменениях')}
							</p>
							<form className="subscribe-form input-form">
								<input
									className="footer__input input-form__input"
									placeholder={t("Введите ваш email")}
								/>
								<button className="input-form__button" onClick={() => handleSubscribeClick()}>
									{t('Отправить')}
								</button>
							</form>
						</div> :
						null
				}
			</div>
			<div className="footer__bottom-block">
				<div className="copyright">
					<p>
						{t('{{ companyName }} ©. Все права защищены.', { companyName })}
					</p>
				</div>
				<div className="politics">
					<a href={usersAgreementLink} target="_blank" rel="noreferrer" className="politics-item">
						{t('Пользовательское соглашение')}
					</a>
					<a href={privacyPolicyLink} target="_blank" rel="noreferrer" className="politics-item">
						{t('Политика конфиденциальности')}
					</a>
				</div>
			</div>
		</div>
	)
}

const mapStateToProps = ({ settings }) => ({
	socialLinks: settings.socialLinks,
	companyInfo: settings.companyInfo,
	subscribeVisibility: settings.visibility.subscribe,
	buttonsUrl: settings.buttonsUrl,
	bottomDocs: settings.bottomDocs,
	logo: settings.branding.logo,
	companyName: settings.branding.companyName,
})

export default connect(
	mapStateToProps,
	null
)(Footer)
