import React from "react";
import {Link} from "react-router-dom";
import {useTranslation} from "react-i18next"
import {useLocation} from "react-router";
import {
	BLOG_PATH, FAQ_PATH,
	FEES_PATH, HOME_PATH,
	HOW_DOES_IT_WORK_PATH,
	PARTNERSHIP_PATH,
	SUPPORT_PATH
} from "../../../common/routerConstants";

const Nav = (props) => {
	const { t } = useTranslation();
	const location = useLocation();
	const { onNavClick } = props;

	const handleLinkClick = (e, path) => {
		if (location.pathname === path) {
			e.preventDefault()
		}
		onNavClick()
	}

	return (
		<ul className="nav">
			<div className="links-container">
				<li className="nav__item ta-right">
					<Link to={HOME_PATH} onClick={(e) => handleLinkClick(e, HOME_PATH)}>P2P</Link>
				</li>
				<li className="nav__item">
					<Link to={FEES_PATH} onClick={(e) => handleLinkClick(e, FEES_PATH)}>{t('Комиссии')}</Link>
				</li>
			</div>
			<div className="links-container">
				<li className="nav__item">
					<Link to={PARTNERSHIP_PATH} onClick={(e) => handleLinkClick(e, PARTNERSHIP_PATH)}>{t("Партнерская программа")}</Link>
				</li>
				<li className="nav__item">
					<Link to={HOW_DOES_IT_WORK_PATH} onClick={(e) => handleLinkClick(e, HOW_DOES_IT_WORK_PATH)}>{t('Как это работает')}</Link>
				</li>
			</div>
			<div className="links-container">
				<li className="nav__item">
					<Link to={SUPPORT_PATH} onClick={(e) => handleLinkClick(e, SUPPORT_PATH)}>{t('Поддержка')}</Link>
				</li>
				<li className="nav__item">
					<Link to={FAQ_PATH} onClick={(e) => handleLinkClick(e, FAQ_PATH)}>FAQ</Link>
				</li>
			</div>
			<div className="links-container">
				<li className="nav__item">
					<Link to={BLOG_PATH} onClick={(e) => handleLinkClick(e, BLOG_PATH)}>{t('Блог')}</Link>
				</li>
			</div>
		</ul>
	)
}

export default Nav;
