import React, {useState} from "react";
import { connect } from 'react-redux';
import {useTranslation} from "react-i18next"

import Nav from "./Nav";
import Lang from "./Lang";
import {HOME_PATH} from "../../common/routerConstants";
import {Link} from "react-router-dom";
import {serverPath} from "../../common/serverPath";

const Header = (props) => {
	const { t, i18n } = useTranslation();
	const { buttonsUrl, logo } = props
	const { login, signup } = buttonsUrl[i18n.language];
	const [menuOpened, setMenuOpened] = useState(false);
	const path = serverPath.methods.getServerPath();

	const handleLogin = () => {
		window.open(login, '_blank')
	}
	const handleSignup = () => {
		window.open(signup, '_blank')
	}

	const handleMobileNavClick = () => {
		if (menuOpened) {
			setMenuOpened(false)
		}
	}

	return (
		<div className={`header ${menuOpened && 'mobile-opened'}`}>
			{
				logo &&
				<Link to={HOME_PATH} className="header__logo">
					<img src={path + logo} alt="logo" />
				</Link>
			}
			<Nav onNavClick={() => handleMobileNavClick()}/>
			<div className="header__right-block">
				<Lang />
				<div className="header__buttons">
					<button
						className="login"
						onClick={() => handleLogin()}
					>
						{t('Вход')}
					</button>
					<button
						className="signup"
						onClick={() => handleSignup()}
					>
						{t('Регистрация')}
					</button>
				</div>
				<button className="mobile-menu" onClick={() => setMenuOpened(!menuOpened)}/>
			</div>

		</div>
	)
}

const mapStateToProps = ({ settings }) => ({
	buttonsUrl: settings.buttonsUrl,
	logo: settings.branding.logo,
})

export default connect(
	mapStateToProps,
	null
)(Header)
