import { call, put, takeEvery } from "redux-saga/effects"

// Crypto Redux States
import { GET_SETTINGS } from "./actionTypes"
import { getSettings } from "../../helpers/backend_helper"
import {getSettingsFail, getSettingsSuccess, toggleLoading} from "./actions"

//Include Both Helper File with needed methods

function* fetchSettings({ payload: searchParams }) {
  try {
    yield put(toggleLoading())
    const response = yield call(getSettings, searchParams)
    yield put(getSettingsSuccess(response))
    yield put(toggleLoading())
  } catch (error) {
    yield put(getSettingsFail(error))
    yield put(toggleLoading())
  }
}

function* usersSaga() {
  yield takeEvery(GET_SETTINGS, fetchSettings)
}

export default usersSaga
