import {
  TOGGLE_LOADING,
  GET_RATES_HISTORY,
  GET_RATES_HISTORY_SUCCESS,
  GET_RATES_HISTORY_FAIL
} from "./actionTypes";

export const toggleLoading = () => ({
  type: TOGGLE_LOADING,
});

export const getRatesHistory = currency => ({
  type: GET_RATES_HISTORY,
  payload: currency,
});

export const getRatesHistorySuccess = (currency, history) => ({
  type: GET_RATES_HISTORY_SUCCESS,
  payload: {
    currency,
    history
  },
});

export const getRatesHistoryFail = (currency, error) => ({
  type: GET_RATES_HISTORY_FAIL,
  payload: {
    currency,
    error
  },
});
