import {Switch, Route, useLocation} from 'react-router';
import './App.scss';
import Header from "./components/Header";
import Home from "./containers/Home";
import Footer from "./components/Footer";
import React, {useEffect} from "react";
import Blog from "./containers/Blog";
import Fees from "./containers/Fees";
import {
	BLOG_PATH,
	FAQ_PATH,
	FEES_PATH,
	HOME_PATH,
	HOW_DOES_IT_WORK_PATH,
	PARTNERSHIP_PATH,
	BLOG_POST_PATH, SUPPORT_PATH
} from "./common/routerConstants";
import Howdoesitwork from "./containers/Howdoesitwork";
import Affiliate from "./containers/Affiliate";
import FAQ from "./containers/FAQ";
import BlogPost from "./containers/Blog/BlogPost";
import Support from "./containers/Support";
import {ToastContainer} from "react-toastify";
import 'react-toastify/dist/ReactToastify.css';
import {getSettings} from "./store/settings/actions";
import {connect} from "react-redux";
import {serverPath} from "./common/serverPath";

function App(props) {
	const location = useLocation();
	const path = serverPath.methods.getServerPath();

	useEffect(() => {
		props.onGetSettings();
		const favicon = document.getElementById("favicon");
		favicon.href = path + props.favIcon
	}, [path, props]);


	const renderBackground = () => {
		const { pathname } = location;
		if (pathname === HOME_PATH) {
			return (
				<React.Fragment>
					<div className="bg-top-line"/>
					<div className="wave-top-right"/>
					<div className="loop"/>
				</React.Fragment>
			)
		}
	}

	const renderBlueGradient = () => {
		const currentLocation = location.pathname;

		if (
			currentLocation.includes(FEES_PATH) ||
			currentLocation.includes(PARTNERSHIP_PATH) ||
			currentLocation.includes(BLOG_PATH) ||
			currentLocation.includes(FAQ_PATH)
		) {
			return 'radial-gradient'
		}
	}

	return (
		<div className={`App ${renderBlueGradient()}`}>
			<Header/>
			<div className="main-wrapper">
				<Switch>
					<Route exact path={BLOG_POST_PATH} component={BlogPost}/>
					<Route exact path={BLOG_PATH} component={Blog}/>
					<Route exact path={FEES_PATH} component={Fees}/>
					<Route exact path={SUPPORT_PATH} component={Support}/>
					<Route exact path={PARTNERSHIP_PATH} component={Affiliate}/>
					<Route exact path={HOW_DOES_IT_WORK_PATH} component={Howdoesitwork}/>
					<Route exact path={FAQ_PATH} component={FAQ}/>
					<Route path={HOME_PATH} component={Home}/>
				</Switch>
			</div>
			<Footer/>
            {renderBackground()}
			<ToastContainer
				position="top-right"
				autoClose={5000}
				hideProgressBar={false}
				newestOnTop={false}
				closeOnClick
				rtl={false}
				pauseOnFocusLoss
				draggable
				pauseOnHover
			/>
		</div>
	);
}

const mapStateToProps = ({ settings }) => ({
	favIcon: settings.branding.favIcon,
})

const mapDispatchToProps = dispatch => ({
	onGetSettings: () => dispatch((getSettings())),
})

export default connect(
	mapStateToProps,
	mapDispatchToProps
)(App)
