import React from 'react'
import {useTranslation,} from "react-i18next";
import {connect} from "react-redux";

import mainBgImage from '../../assets/images/bg-main.png';

const MainInfo = (props) => {
	const { t, i18n } = useTranslation();
	const { start } = props.buttonsUrl[i18n.language];

	const handleStart = () => {
		window.open(start, '_blank')
	}
	return (
		<section className="main-info">
			<div className="main-info__info">
				<h1 className="main-info__title">
					{t('Криптовалютная платформа для трейдинга')}
				</h1>
				<p className="main-info__subtitle">
					{t('Гарантия минимальных комиссий, с высокой скоростью, автоматическими выводами и полной безопасностью')}
				</p>
				<button
					className="main-info__button"
					onClick={() => handleStart()}
				>
					{t('Начать сейчас')}
				</button>
			</div>
			<div className="main-info__image">
				<img src={mainBgImage} alt="P2P" />
			</div>
		</section>
	)
}

const mapStateToProps = ({ settings }) => ({
	buttonsUrl: settings.buttonsUrl,
})

export default connect(
	mapStateToProps,
)(MainInfo)
