import { GET_SETTINGS_FAIL, GET_SETTINGS_SUCCESS } from "./actionTypes"
import {TOGGLE_LOADING} from "./actionTypes";

const INIT_STATE = {
  maintenance: false,
  buttonsUrl: {
    ru: {
      homePage: '',
      topUpWallet: '',
      login: '',
      signup: '',
      start: ''
    },
    en: {
      homePage: '',
      topUpWallet: '',
      login: '',
      signup: '',
      start: ''
    },
    sp: {
      homePage: '',
      topUpWallet: '',
      login: '',
      signup: '',
      start: ''
    },
    fr: {
      homePage: '',
      topUpWallet: '',
      login: '',
      signup: '',
      start: ''
    },
    id: {
      homePage: '',
      topUpWallet: '',
      login: '',
      signup: '',
      start: ''
    },
  },
  companyInfo: {
    name: '',
    address_line_one: '',
    address_line_two: '',
  },
  visibility: {
    magazines: true,
  },
  branding: {
    logo: '',
    favIcon: '',
    companyName: '',
  },
  bottomDocs: [],
  socialLinks: [],
  video: [],
  loading: false,
  error: null,
}

const settings = (state = INIT_STATE, action) => {
  const { payload } = action;
  switch (action.type) {
    case TOGGLE_LOADING:
      return {
        ...state,
        loading: !state.loading,
      }

    case GET_SETTINGS_SUCCESS:
      return {
        ...state,
        maintenance: payload.settings.maintenance,
        buttonsUrl: payload.settings.buttonsUrl,
        visibility: payload.settings.visibility,
        companyInfo: payload.companyInfo,
        socialLinks: payload.socialLinks,
        bottomDocs: payload.bottomDocs,
        video: payload.video,
        branding: payload.branding,
      }

    case GET_SETTINGS_FAIL:
      return {
        ...state,
        error: action.payload,
      }

    default:
      return state;
  }
}

export default settings;
