import { get } from "./api_helper"
import * as url from "./url_helper"

const insertParamInURL = (url, param) => {
  if (url.indexOf('/', url.indexOf(':')) === -1) {
    return url.replace(url.slice(url.indexOf(':')), param)
  }
  return url.replace(url.slice(url.indexOf(':'), url.indexOf('/', url.indexOf(':'))), param)
}

export const getSettings = () => get(url.GET_SETTINGS)

export const getNews = () => get(url.GET_NEWS)

export const getAffiliate = () => get(url.GET_AFFILIATE_PROGRAM)

export const getBlogPosts = (lang) => get(insertParamInURL(url.GET_BLOG_ALL, lang))
export const getBlogPost = (lang, uri) => get(url.GET_BLOG_POST.replace(':lang', lang).replace(':uri', uri))

export const getFaq = (lang) => get(insertParamInURL(url.GET_FAQ, lang))

export const getDocs = (lang) => get(insertParamInURL(url.GET_DOCS, lang))

export const getFees = () => get(url.GET_FEES)

export const getRatesHistory = (currency) => fetch(url.COINGECKO_HISTORY_URL.replace(':currency', currency)).then(res => res.json())
