import React, {useLayoutEffect, useState, useEffect} from "react";
import SwiperCore, { Pagination } from 'swiper';
import { Swiper, SwiperSlide } from 'swiper/react';

import 'swiper/swiper.scss';
import 'swiper/components/pagination/pagination.scss';
import Slide from "../../components/Slide";

import btcIcon from '../../assets/images/bitcoin-logo.svg'
import ltcIcon from '../../assets/images/ltc-logo.svg'
import usdtIcon from '../../assets/images/tether-logo.svg'
import ethIcon from '../../assets/images/eth-logo.svg'

import plusGraphOne from '../../assets/images/plus-graph-one.svg'
import plusGraphTwo from '../../assets/images/plus-graph-two.svg'
import minusGraphOne from '../../assets/images/minus-graph-one.svg'
import minusGraphTwo from '../../assets/images/minus-graph-one.svg'

SwiperCore.use([Pagination]);

const plusGraphs = [plusGraphOne, plusGraphTwo]
const minusGraphs = [minusGraphOne, minusGraphTwo]

const coinsInit = {
	bitcoin: {
		currency: 'Bitcoin',
		currKey: 'BTC',
		change: 0,
		icon: btcIcon,
	},
	litecoin: {
		key: 'litecoin',
		currency: 'Litecoin',
		currKey: 'LTC',
		change: 0,
		icon: ltcIcon,
	},
	ethereum: {
		key: 'ethereum',
		currency: 'Ethereum',
		currKey: 'ETH',
		change: 0,
		icon: ethIcon,
	},
	tether: {
		key: 'tether',
		currency: 'Tether',
		currKey: 'USDT',
		change: 0,
		icon: usdtIcon,
	},
}


const Carousel = () => {

	const [size, setSize] = useState(window.innerWidth);
	const [ticker, setTicker] = useState(true);
	const [coinsList, setCoinsList] = useState(coinsInit);

	const updateRates = () => {
		fetch('https://api.coingecko.com/api/v3/simple/price?ids=bitcoin%2Clitecoin%2Cethereum%2Ctether&vs_currencies=usd&include_24hr_change=true')
			.then((res) => res.json())
			.then((rates) => {
				let newList = {
					...coinsList
				}
				// eslint-disable-next-line array-callback-return
				Object.keys(rates).map((rate) => {
					newList[rate] = {
						...newList[rate],
						price: rates[rate].usd,
						change: rates[rate].usd_24h_change,
					}
				})
				setCoinsList(newList)
				setTimeout(() => setTicker(true), 1000 * 60 * 10)
			})
	}

	useEffect(() => {
		if (ticker) {
			setTicker(false)
			updateRates()
		}
	}, [ticker]);  // eslint-disable-line react-hooks/exhaustive-deps


	useLayoutEffect(() => {
		function updateSize() {
			setSize(window.innerWidth);
		}
		window.addEventListener('resize', updateSize);
		updateSize();
		return () => window.removeEventListener('resize', updateSize);
	}, []);

	const slidesPerView = () => {
		if (size < 560) {
			return 1;
		}
		if (size < 870) {
			return 2;
		}
		if (size < 1140) {
			return 3;
		}
		return 4;
	}
	const slidesMargin = () => {
		if (size < 560) {
			return 0;
		}
		return 24;
	}

	return (
		<Swiper
			tag="section"
			wrapperTag="ul"
			className="carousel"
			pagination={{ clickable: true }}
			slidesPerView={slidesPerView()}
			spaceBetween={slidesMargin()}
		>
			{/*Hardcode for torexo*/}
			<SwiperSlide
				tag="li"
				key="slide-torexo"
			>
				<Slide
					id="torexo"
					icon={null}
					diff={0}
					currency="Torexo USD"
					currKey="TD"
					price="1"
				/>
			</SwiperSlide>
			{
				coinsList ?
				Object.keys(coinsList).map((coin) => {
					const coinData = coinsList[coin]
					return (
					<SwiperSlide
						tag="li"
						key={`slide-${coin}`}
					>
						<Slide
							id={coin}
							icon={coinData.icon}
							graph={coinData.change < 0 ? minusGraphs[Math.floor(Math.random() * minusGraphs.length)] : plusGraphs[Math.floor(Math.random() * plusGraphs.length)]}
							diff={parseFloat(coinData.change.toFixed(2))}
							currency={coinData.currency}
							currKey={coinData.currKey}
							price={coinData.price}
						/>
					</SwiperSlide>
				)}) :
					null
			}
		</Swiper>
	)
}

export default Carousel;
