import React, {useEffect, useState} from "react";
import {Trans, useTranslation} from "react-i18next";
import {getAffiliate} from "../../helpers/backend_helper";
import {connect} from "react-redux";
import {toast} from "react-toastify";

const affiliateInit = {
	firstLineMembers: 0,
	firstLinePercent: 0,
	secondLineMembers: 0,
	secondLinePercent: 0,
	thirdLineMembers: 0,
	thirdLinePercent: 0,
}

const Affiliate = (props) => {
	const { t } = useTranslation();
	const { companyName } = props;
	const [affiliate, setAffiliate] = useState(affiliateInit);


	useEffect(() => {
		getAffiliate()
			.then((res) => {
				if (!res.length) {
					toast.warning('Укажите значения для партнерской программы в админ. панели')
					return
				}
				setAffiliate(res[0])
			})
	}, []);


	return (
		<section className="affiliate">
			<h1 className="affiliate__title">
				{t('Партнерская программа {{ companyName }}', { companyName })}
			</h1>
			<div className="affiliate__info info">
				<p className="info__text">
					<Trans>Для всех пользователей существует <b>трехступенчатая программа лояльности</b>, которая напрямую зависит от количества приглашенных вами людей и совершённых с их аккаунта сделок, при отсутствии мошеннических действий</Trans>
				</p>
				<p className="info__text">
					<Trans>Вы даете <b>ссылку</b> вашим друзьям, им нужно пройти регистрация и совершать операции, вы получаете процент от их комиссии!</Trans>
				</p>
				<p className="info__text">
					{t('Где Х зависит напрямую от количества приглашенных вами людей!')}
				</p>
				<p className="info__text">
					<Trans>Внимание, <b>считаются только активные пользователи</b>, совершающие <b>минимум 3 сделки в месяц!</b></Trans>
				</p>
				<ul className="info__list list">
					<li className="list__item item">
						<div className="item__container left">
							<p className="info__text">
								{t('При приглашении')} <b>{affiliate.firstLineMembers} {t('друзей')}</b>
							</p>
						</div>
						<div className="item__container right">
							<p className="info__text">
								X = {affiliate.firstLinePercent}&#37;
							</p>
						</div>
					</li>
					<li className="list__item item">
						<div className="item__container left">
							<p className="info__text">
								{t('При приглашении')} <b>{affiliate.secondLineMembers} {t('друзей')}</b>
							</p>
						</div>
						<div className="item__container right">
							<p className="info__text">
								X = {affiliate.secondLinePercent}&#37;
							</p>
						</div>
					</li>
					<li className="list__item item">
						<div className="item__container left">
							<p className="info__text">
								{t('При приглашении')} <b>{affiliate.thirdLineMembers} {t('друзей')}</b>
							</p>
						</div>
						<div className="item__container right">
							<p className="info__text">
								X = {affiliate.thirdLinePercent}&#37;
							</p>
						</div>
					</li>
				</ul>
				<p className="info__text">
					{t('Неактивные пользователи в программе лояльности не учитываются!')}
				</p>
			</div>
		</section>
	)

}

const mapStateToProps = ({ settings }) => ({
	companyName: settings.branding.companyName,
})

export default connect(
	mapStateToProps,
	null
)(Affiliate)

