import {
  GET_SETTINGS,
  GET_SETTINGS_SUCCESS,
  GET_SETTINGS_FAIL,
  TOGGLE_LOADING
} from "./actionTypes";

export const toggleLoading = () => ({
  type: TOGGLE_LOADING,
});

export const getSettings = searchParams => ({
  type: GET_SETTINGS,
  payload: searchParams,
});

export const getSettingsSuccess = transactions => ({
  type: GET_SETTINGS_SUCCESS,
  payload: transactions,
});

export const getSettingsFail = error => ({
  type: GET_SETTINGS_FAIL,
  payload: error,
});
