import React from "react";
import {useTranslation} from "react-i18next";
import {connect} from "react-redux";
import {P2P_HOME_URL} from "../../helpers/url_helper";

const Bonus = (props) => {
	const { t } = useTranslation();

	const handleSubmit = (e) => {
		e.preventDefault();
		window.open(`${P2P_HOME_URL}register?email=${e.target.email.value}`, '_blank');
		e.target.email.value = '';
	}

	return (
		<section className="bonus">
			<p className="bonus__subtitle section-description">
				{t('Готовы начать торговать криптовалютой?')}
			</p>
			<h2 className="bonus__title section-title">
				{t('Новые пользователи могут заработать до $80 в виде вознаграждений')}
			</h2>
			<form
				className="bonus__form input-form"
				onSubmit={(e) => handleSubmit(e)}
			>
				<input
					className="bonus__input input-form__input"
					name="email"
					placeholder={t('Введите ваш email')}
				/>
				<button
					className="input-form__button"
					type="submit"
				>
					{t('Начать')}
				</button>
			</form>
		</section>
	)
}

const mapStateToProps = ({ settings }) => ({
	buttonsUrl: settings.buttonsUrl,
})

export default connect(
	mapStateToProps,
)(Bonus)
