import React, {useEffect, useState} from "react";
import {useTranslation} from "react-i18next";
import {getFees} from "../../helpers/backend_helper";
import {connect} from "react-redux";
import {toast} from "react-toastify";

const Fees = (props) => {
	const { t } = useTranslation();
	const { companyName } = props;
	const [fees, setFees] = useState(null);

	useEffect(() => {
		getFees()
			.then((res) => {
				if (!res.length) {
					toast.warning('Укажите значения комиссий в админ. панели')
					return
				}
				setFees(res[0])
			})
	}, []);


	return (
		<section className="fees">
			<div className="fees__info">
				<h1 className="fees__title">
					{t('Комиссии')}
				</h1>
				<div className="fees__block-one block-one">
					<h3 className="block-one__title">
						{t('Всего 3 типа комиссий в {{ companyName }}', { companyName })}:
					</h3>
					<ol className="block-one__list list">
						<li className="list__item">
							{t('Комиссия за ввод')}
						</li>
						<li className="list__item">
							{t('Комиссия за вывод')}
						</li>
						<li className="list__item">
							{t('Комиссия за обмен на P2P')}
						</li>
					</ol>
				</div>
				<div className="fees__block-two block-two">
					<h4 className="block-two__title">
						{t('Комиссия за ввод в {{ companyName }}', { companyName })}:
					</h4>
					<p className="block-two__text mb">
						{t('Комиссия за ввод')} = <b>{fees ? fees.depositFee : '0'}%</b>
					</p>
					<h4 className="block-two__title">
						{t('Комиссия за вывод c {{ companyName }}', { companyName })}:
					</h4>
					<p className="block-two__text">
						BTC: <b>{fees ? fees.withdrawalFee.btc : '0'}%</b> + {t('комиссия сети')}
					</p>
					<p className="block-two__text">
						USDT (ERC 20): <b>{fees ? fees.withdrawalFee.usdt : '0'}%</b> + {t('комиссия сети')} ETH
					</p>
					<p className="block-two__text">
						LTC: <b>{fees ? fees.withdrawalFee.ltc : '0'}%</b> + {t('комиссия сети')} LTC
					</p>
					<p className="block-two__text">
						ETH: <b>{fees ? fees.withdrawalFee.eth : '0'}%</b> + {t('комиссия сети')} ETH
					</p>
				</div>
			</div>
		</section>
	)
}

const mapStateToProps = ({ settings }) => ({
	companyName: settings.branding.companyName,
})

export default connect(
	mapStateToProps,
	null
)(Fees)
